import Head from "next/head";

import Container from "@/src/components/carbon/Container";
import ItemTabs from "@/src/components/item/Tabs";
import { GetStaticProps, InferGetStaticPropsType } from "next";
import { useEffect } from "react";
import { useRouter } from "next/router";
import { FlexGrid, Row, Column } from "@carbon/react";

export const getStaticProps: GetStaticProps<{}> = async () => {
  return { props: {} };
};

export default function Item({}: InferGetStaticPropsType<
  typeof getStaticProps
>) {
  const router = useRouter();

  const hash = router.asPath.split("#")[1] || "";
  const url = `https://sandbox.nftxledger.com/${hash}`;

  const image_url = `https://portal.nftxledger.com/${hash}/thumbnail`;

  const hash_title = "#" + hash;

  useEffect(() => {}, []);

  return (
    <>
      <Head>
        <title>NFTxLedger - View Item</title>
        <meta name="description" content="View Item" />
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <link rel="icon" href="/favicon.ico" />
      </Head>
      <Container title={hash_title}>
        <FlexGrid>
          <Row>
            <div style={{ border: "1px solid black" }}>
              <iframe src={url} width="500px" height="500px"></iframe>
            </div>
          </Row>
          <Row>
            <ItemTabs></ItemTabs>
          </Row>
        </FlexGrid>
      </Container>
    </>
  );
}
